<template>
  <figure class="avatar" :style="avatarStyle">
    <img
      v-if="src !== 'undefined'"
      :src="src"
      :style="{ width: props.size, height: props.size }"
    />
    <GlobalContainerAvatar
      v-else
      background-color="type-directory"
      :radius="iconProps.radius"
      :container-size="iconProps.size"
    >
      <IPerson :size="iconProps.iconSize" />
    </GlobalContainerAvatar>
    <div
      :class="[props.status ? `status ${props.status}` : '']"
      style="z-index: 2"
    ></div>
  </figure>
</template>

<script setup lang="ts">
import { computed } from 'vue';
import { getAllNumbers, getMeasureUnit } from '@/utils';
import type { UserStatusEnum } from '@/types';
import GlobalContainerAvatar from '@/components/Global/GlobalContainerAvatar.vue';
import IPerson from '@/components/Icons/IPerson.vue';

interface Props {
  status?: UserStatusEnum;
  size?: string;
  src?: string;
  name?: string | null;
  rounded?: boolean;
}

const props = withDefaults(defineProps<Props>(), {
  size: '1rem',
  rounded: false,
  src: '',
  name: '',
  status: undefined,
});

const avatarStyle = computed(() => {
  const size = getAllNumbers(props.size as string);
  const unit = getMeasureUnit(props.size as string);
  const fontSize = parseInt(size, 10) * 0.333;

  return {
    width: props.size,
    height: props.size,
    fontSize: `${fontSize}${unit}`,
    borderRadius: `${iconProps.value.radius}px`,
  };
});

const iconProps = computed(() => {
  switch (props.size) {
    case '1.25rem':
      return {
        size: '20',
        radius: '4',
        iconSize: '12',
      };
    case '1.5rem':
      return {
        size: '24',
        radius: '5',
        iconSize: '15',
      };
    case '2rem':
      return {
        size: '32',
        radius: '5',
        iconSize: '18',
      };
    case '2.25rem':
      return {
        size: '36',
        radius: '8',
        iconSize: '21',
      };
    default:
      return {
        size: '16',
        radius: '4',
        iconSize: '9',
      };
  }
});
</script>

<style scoped lang="scss">
@import '@/assets/sass/chat.scss';
</style>
